
import BScroll from 'better-scroll'
import Rater from 'vux/src/components/rater/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

let count = 1
export default {
name: 'hotelManage',
directives: {
TransferDom
},
components: {
Rater,
Previewer,
XTextarea,
Group,
XDialog
},
data () {
return {
currentCommentId: '',
commentsValue: '',
showToast: false,
isMoreData: true,
pulldownMsg: '下拉刷新',
pullupMsg: '加载更多',
alertHook: 'none',
baseInfo: {
postDetail: {
postName: '',
postDescription: ''
},
commentInfo: {
commentNumber: 0,
point: 0,
comments: 0
}
},
params: {
merchantId: '',
page: 1,
pageSize: 10
},
data3: 4.5,
data: [],
list: [{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
w: 800,
h: 400
},
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
w: 1200,
h: 900
}, {
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwwcynw2j20p00b4js9.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwwcynw2j20p00b4js9.jpg'
}, {
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwu9ze86j20m80b40t2.jpg',
w: 800,
h: 400
},
{
msrc: 'http://ww1.sinaimg.cn/thumbnail/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
src: 'http://ww1.sinaimg.cn/large/663d3650gy1fplwvqwuoaj20xc0p0t9s.jpg',
w: 1200,
h: 900
}],
previewList: [],
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
methods: {
showDialog(id) {
this.currentCommentId = id
this.showToast = true
},
replyComment () {
if(!this.commentsValue){
this.$vux.toast.show({
text: '请输入内容',
position: 'middle',
type: 'warn'
})
return
}
this.$http.fetchPost('/merchant/post/add/commentreply',{commentId: this.currentCommentId,merchantId:this.$store.state.merchantId,replyText:this.commentsValue}).then((res)=>{
if(res.data.code == 200) {
this.$vux.toast.show({
text: '评论成功',
position: 'middle',
type: 'warn'
})
this.data.map((item)=>{
if(item.id === this.currentCommentId){
item.replyText = this.commentsValue
}
})
this.currentCommentId = ''
this.commentsValue = ''
this.showToast = false
}else{
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
editHotel () {
this.$router.push({
name: 'editHotel'
})
},
coupons(){
this.$router.push({
name: 'coupons'
})
},
show (parent,index) {
this.previewList = this.data[parent].commentImageInfo
this.previewList.map((item)=>{
item.msrc = item.src = item.url
item.w = item.h = 0
})
this.$nextTick(()=>{
this.$refs.previewer.show(index)
})
},
getBack () {
this.$router.go(-1)
},
getData () {
let that = this
return new Promise(resolve => { // 模拟数据请求
setTimeout(() => {
that.isMoreData = false
console.log(that.isMoreData)
const arr = []
for (let i = 0; i < 26; i++) {
arr.push(count++)
}
resolve(arr)
}, 1000)
})
},
getInfo () {

this.$http.fetchGet('/merchant/post/get/main', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.baseInfo = res.data.data
this.data = res.data.data.commentInfo.comments
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}
this.initScroll()
})
},
refreshData () {
this.$refs.content.style.height = 'auto'
this.pullupMsg = '加载中。。。'
this.params.page = 1
this.$http.fetchGet('/merchant/post/get/main', this.params).then((res) => {
this.baseInfo = res.data.data
this.data = res.data.data.commentInfo.comments
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
this.$nextTick(() => {
if (this.$refs.list_con.offsetHeight > this.$refs.content.offsetHeight) {
this.$refs.content.style.height = this.$refs.list_con.offsetHeight + 2 + 'px'
}
})
}

this.pullupMsg = '加载更多'

this.scroll.refresh()
})
},
loadMoreData () {
if (this.data.length < this.params.pageSize || this.data.length < this.params.pageSize * this.params.page) {
this.isMoreData = false
return
}
this.$vux.loading.show({
text: '加载中...'
})
this.params.page++
this.$http.fetchGet('/merchant/post/get/main', this.params).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.baseInfo = res.data.data
if (this.data.length === this.params.pageSize) {
this.isMoreData = true
} else {
this.isMoreData = false
}
if (res.data.data.commentInfo.comments.length > 0) {
res.data.data.commentInfo.comments.map((item) => {
this.data.push(item)
})
} else {
this.isMoreData = false
}

this.pulldownMsg = '下拉刷新'

this.scroll.refresh()
})
},
initScroll () {
this.$nextTick(() => {
this.scroll = new BScroll(this.$refs.wrapper, { // 初始化better-scroll
probeType: 1, // 1 滚动的时候会派发scroll事件，会截流。2滚动的时候实时派发scroll事件，不会截流。 3除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
click: true 
})

this.scroll.on('scroll', (pos) => {
if (pos.y > 30) {
this.pulldownMsg = '释放立即刷新'
}
})

this.scroll.on('touchEnd', (pos) => { // 上拉刷新
if (pos.y > 30) {
this.refreshData()
} else if (pos.y < (this.scroll.maxScrollY - 30)) { // 下拉加载
this.loadMoreData()
}
})
})
},
},
created () {
this.$vux.loading.show({
text: '加载中...'
})
this.params.merchantId = this.$store.state.merchantId
this.getInfo()
}
}



